<template>
  <ve-histogram :data="chartData" :settings="chartSettings" :legend='legend'></ve-histogram>
</template>

<script>
export default {
    data() {
    return {
      chartSettings: { 
       },
       legend:{
         bottom: '30px'
       },
      chartData: {
        columns: ['Year', 'Mon', 'Tue', 'Wed', 'Thur', 'Fri', 'Sat', 'Sun'],
        rows: []
      }
    }
  },
   methods:{
    getData(){
      this.axios.post('http://localhost/api/manager/appointment_statistics.php').then(res=>{
        var data = res.data
        var rows = []
        var data = {}
        data['Year'] = 2020
        data['Mon'] = res.data[0]
        data['Tue'] = res.data[1]
        data['Wed'] = res.data[2]
        data['Thur'] = res.data[3]
        data['Fri'] = res.data[4]
        data['Sat'] = res.data[5]
        data['Sun'] = res.data[6]
        rows.push(data);
        this.chartData.rows = rows
        this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Get histogram chart data successfully',
            description:
            'Using ' + res.data[7].toFixed(4) + 's',
            duration: 2,
            placement: 'topLeft'
        });
      })
    }
  },
  mounted(){
    this.getData()
  }
}
</script>