<template>
    <a-card title="Rates" hoverable>
        <a-row :gutter="16">
            <a-col :span="12">
                <a-statistic title="Average Rates" :value="aver_rates" style="margin-right: 50px">
                <template #suffix>
                    <a-icon type="like" />
                </template>
                </a-statistic>
            </a-col>
            <a-col :span="12">
                <a-statistic title="Participation Ratio" :value="pRatio" class="demo-class">
                <template #suffix>
                    <span> / {{total_num}}</span>
                </template>
                </a-statistic>
            </a-col>
        </a-row>
    </a-card>
</template>

<script>
export default {
    data(){
        return {
            aver_rates: 0,
            pRatio: 0,
            total_num: 0
        }
    },
    methods:{
        getRate(){
            this.axios.post('http://localhost/api/manager/average_rates.php').then(res=>{
                var data = res.data[0][0]
                this.aver_rates = data.average_number
                this.total_num = data.rates_number
                this.pRatio = data.user_number
                this.$notification.open({
                    icon: <a-icon type="smile" style="color: #108ee9" />,
                    message: 'Calculate rates successfully',
                    description:
                    'Using ' + res.data[1] + 's',
                    duration: 2,
                });
            })
        }
    },
    mounted(){
        this.getRate()
    }
}
</script>
