<template>
  <a-calendar class="cStyle" :fullscreen="false" @select="onSelect">
    <ul slot="dateCellRender" slot-scope="value" class="events">
      <li v-for="item in getListData(value)" :key="item.content">
        <a-badge :status="item.type" />
      </li>
    </ul>
  </a-calendar>
</template>
<script>
import { mapMutations } from 'vuex';
export default {
  data(){
    return{
      month: '',
      date: ''
    }
  },
  methods: {
    ...mapMutations(['storeDate', 'storeDay']),
    onSelect(value) {
      let diffnum = -this.$moment(this.$moment()).diff(this.$moment(value), 'days')
      if(diffnum <= 6 && diffnum >= 0 && this.$moment().date() != value.date()){
        var params = {}
        var week = this.getWeek(value)
        params['date'] = value.format('YYYYMMDD')
        params['week'] = week
        this.storeDate({ date: params.date })
        this.storeDay({ day: params.week })
        this.$router.push({
          name: 'Select',
        }).catch(() =>{})
      }
    },
    getListData(value) {
      let listData;
      let diffnum = -this.$moment(this.$moment()).diff(this.$moment(value), 'days')
      if(diffnum <= 6 && diffnum >= 0 && this.$moment().date() != value.date()){
        listData = [
            { type: 'success'}
          ];
      }
      return listData || [];
    },
    getWeek (value) {
      let week = value.day()
      switch (week) {
        case 1:
          return 'Mon'
        case 2:
          return 'Tue'
        case 3:
          return 'Wed'
        case 4:
          return 'Thur'
        case 5:
          return 'Fri'
        case 6:
          return 'Sat'
        case 0:
          return 'Sun'
      }
    }
  }
};
</script>
<style>
  .cStyle{
    position: absolute;
    left: 25%;
    width: 50%;
  }
  .events .ant-badge-status {
    overflow: hidden;
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    font-size: 12px;
  }
  .notes-month {
    text-align: center;
    font-size: 28px;
  }
  .notes-month section {
    font-size: 28px;
  }
</style>
