<template>
  <div>
      <a-form-model ref="ruleForm" :model="ruleForm" :rules="rules" v-bind="layout">
        <a-form-model-item has-feedback label="Username" prop="name">
            <a-input v-model.number="ruleForm.name" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="ID " prop="ID">
            <a-input v-model="ruleForm.ID" autocomplete="on" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="Phone" prop="pNumber">
            <a-input v-model="ruleForm.pNumber" autocomplete="on"/>
        </a-form-model-item>                                
        <a-form-model-item label="Gender">
            <a-radio-group v-model="ruleForm.gender">
            <a-radio value="1">
                Male
            </a-radio>
            <a-radio value="0">
                Female
            </a-radio>
            </a-radio-group>
        </a-form-model-item>
        <a-form-model-item has-feedback label="Age" prop="age">
            <a-input v-model.number="ruleForm.age" />
        </a-form-model-item>
        <a-form-model-item label="Marry">
            <a-radio-group v-model="ruleForm.marry">
            <a-radio value="1">
                Marreied
            </a-radio>
            <a-radio value="0">
                Not Marreied
            </a-radio>
            </a-radio-group>
        </a-form-model-item>
        <a-form-model-item has-feedback label="Password" prop="pass">
            <a-input v-model="ruleForm.pass" type="password" autocomplete="off" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="Confirm" prop="checkPass">
            <a-input v-model="ruleForm.checkPass" type="password" autocomplete="off" />
        </a-form-model-item>
        <a-form-model-item :wrapper-col="{ span: 14, offset: 7 }">
            <a-button type="primary" @click="submitForm('ruleForm')">
                Submit
            </a-button>
            <a-button style="margin-left: 10px" @click="resetForm('ruleForm')">
                Reset
            </a-button>
        </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
export default {
    data() {
    let checkPending;
    let checkname = (rule, value, callback) => {
      clearTimeout(checkPending);
      if (!value) {
        return callback(new Error('Please input the name'));
      } else {
        callback();
      }
    };
    let checkid = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the ID Number'));
      } else {
          var i = value
          var l=0;
          while(i >= 1){
            i=i/10;
            l++;
          }
          if (l !== 15) {
            callback(new Error("Please input correct ID number"));
          } else {
            callback();
          }
      }
    };
    let checkPhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the Phone Number'));
      } else {
          var i = value
          var l=0;
          while(i >= 1){
            i=i/10;
            l++;
          }
          if (l !== 11) {
            callback(new Error("Please input correct Phone number"));
          } else {
            callback();
          }
      }
    };
    let checkAge = (rule, value, callback) => {
      clearTimeout(checkPending);
      if (!value) {
        return callback(new Error('Please input the age'));
      }
      checkPending = setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Please input digits'));
        } else {
          if (value < 14 || value > 70) {
            callback(new Error('Age must be greater than 14, smaller than 70'));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password again'));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("Two inputs don't match!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        pass: '',
        checkPass: '',
        ID:'',
        name: '',
        gender: '',
        marry: '',
        age: '',
        pNumber: ''
      },
      rules: {
        pass: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        ID: [{ validator: checkid, trigger: 'change' }],
        name: [{ validator: checkname, trigger: 'change' }],
        age: [{ validator: checkAge, trigger: 'change' }],
        pNumber: [{ validator: checkPhone, trigger: 'change' }],
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        var params = new URLSearchParams()
        params.append('name', this.ruleForm.name)
        params.append('password', this.ruleForm.pass)
        params.append('gender', this.ruleForm.gender)
        params.append('idnumber', this.ruleForm.ID)
        params.append('age', this.ruleForm.age)
        params.append('marital_status', this.ruleForm.marry)
        params.append('phone_number', this.ruleForm.pNumber)
        if (valid) {
            this.axios.post('http://localhost/api/user/userInfo/addUser.php',params).then(res=>{
              if(res.data[0] == 200){
                this.$router.push({
                    name: 'Login',
                    }).catch(() =>{})
                this.$notification['success']({
                    message: 'Success',
                    description:
                    'Rejisted user Successfully!Using ' + res.data[1].toFixed(4) + 's. Please log it again!',
                })
              } else {
                this.$notification['error']({
                      message: 'Error',
                      description:
                      'Error Server!',
                  })
              }
            })
        } else {
          // console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
}
</script>

<style>
    .registedStyle{
        position: relative;
        left: 40px;
    }
</style>