<template>
  <div>
    <div>
      <a-descriptions class="userStyle" title="Nucleic Acid Testing Report" :column="6" bordered>
        <a-descriptions-item label="Name" :span='2'>
          {{info.name}}
        </a-descriptions-item>
        <a-descriptions-item label="Age" :span='2'>
          {{info.age}}
        </a-descriptions-item>
        <a-descriptions-item label="Gender" :span='2'>
          {{info.gender}}
        </a-descriptions-item>
        <a-descriptions-item label="ID Number" :span='6'>
          {{info.idnumber}}
        </a-descriptions-item>
        <a-descriptions-item label="Date" :span='3'>
          {{info.date}}
        </a-descriptions-item>
        <a-descriptions-item label="Is Positive?" :span='3'>
          <a-badge v-show="badgeShow" status="processing" :color="color"/>
            {{info.isPositive}}
        </a-descriptions-item>
        <a-descriptions-item label="Print" :span='3'> 
          <a-button @click="showModal">Click</a-button>
        </a-descriptions-item>
      </a-descriptions>
    </div>
  </div>
</template>
<script>
import userInfo from '@/components/userInfo'
export default {
  data(){
    return{
      info:{
        name: '',
        idnumber: '',
        isPositive: '',
        age: '',
        gender: '',
        date:''
      },
      color: '',
      badgeShow: false
    }
  },
  components:{
    userInfo
  },
  methods:{
    showModal(){
       this.$notification['warning']({
        message: 'Warning',
        description:
          'You should print this at the designated hospital location!',
      });
    },
    getList(){
      var params = new URLSearchParams()
      params.append('idnumber', localStorage.getItem('Authorization'))
      params.append('date', this.$moment().format('YYYYMMDD'))
      this.axios.post('http://localhost/api/user/Covid/result.php',params).then(res=>{
        var data = res.data[0][0]
        this.info.name = data.name
        this.info.age = data.age
        if (data.gender == 1){
          this.info.gender = "Male"
        } else {
          this.info.gender = "Female"
        }
        if(res.data[1].length != 0){
          data = res.data[1][0]
          this.info.date = data.date
          this.badgeShow = true
          if(data.isPositive == 1){
            this.info.isPositive = 'True'
            this.color = 'red'
          } else {
            this.info.isPositive = 'False'
            this.color = 'green'
          }
        }
        this.info.idnumber = localStorage.getItem('Authorization')
        this.$message.success('Search Successfully! Using ' + res.data[2].toFixed(4) + 's');
      })
    }
  },
  mounted(){
    this.getList()
  }
}
</script>
<style scoped>
  .userStyle{
      position: absolute;
      left: 30%;
      top: 20%;
      width: 700px;
  }
</style>