import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/users/Login'
import Main from '@/views/main/Main'
import Registed from '@/views/users/Registed'
import ForgetPassword from '@/views/users/ForgetPassword'
import notfound from '@/components/404'
import Select from '@/components/Select'
import Doctor from '@/components/Doctor'

import Controller from '@/views/Controller'
import AR from '@/views/main/RS/AR'
import OP from '@/views/main/RS/OP'
import RR from '@/views/main/RS/RR'
import PI from '@/views/main/PI'

import Appointment from '@/views/main/COVID-19/appointment'
import Result from '@/views/main/COVID-19/result'
import VV from '@/views/main/COVID-19/vaccine'

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      keepalive: false
    }
  },
  {
    path: '/Registed',
    name: 'Regist',
    component: Registed,
    meta: {
      keepalive: true,
      skeepalive: false
    }
  },
  {
    path: '/ForgetPassword',
    name: 'ForgetPassword',
    component: ForgetPassword,
    meta: {
      keepalive: true,
      skeepalive: false
    }
  },
//__________________________________________________________________________
  {
    path: '/Doctor',
    name: 'Doctor',
    component: Doctor,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/Main',
    name: 'Main',
    component: Main,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
//__________________________________________________________________________
  {
    path: '/VV',
    name: 'VV',
    component: VV,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/Appointment',
    name: 'Appointment',
    component: Appointment,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/Result',
    name: 'Result',
    component: Result,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
//__________________________________________________________________________
  {
    path: '/AR',
    name: 'AR',
    component: AR,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/OP',
    name: 'OP',
    component: OP,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/RR',
    name: 'RR',
    component: RR,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/PI',
    name: 'PI',
    component: PI,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
  {
    path: '/Select',
    name: 'Select',
    component: Select,
    meta: {
      keepalive: true,
      skeepalive: true
    }
  },
//__________________________________________________________________________
  {
    path: '/Controller',
    name: 'Controller',
    component: Controller,
    meta: {
      keepalive: true,
      skeepalive: false
    }
  },
//__________________________________________________________________________
  {
    path: '/404',
    name: '404',
    component: notfound,
    meta: {
      keepalive: false
    }
  },
  {
   path: '*',
   redirect: '/404'
  }
]


const router = new VueRouter({
  // mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
    if(to.path === '/login') {
      next();
    } else {
      let token = localStorage.getItem('Authorization');
      if(token === null || token === ''|| token === "undefined") {
        next('/login');
      } else {
        next();
      }
    }
});

export default router
