<template>
    <div>
        <a-row type="flex" justify="space-around" >
            <img class="imgStyle" src="/back.jpg"/>
            <a-col :span="11" :offset="13">
                <a-card 
                 :bordered="false" 
                 class="cardStyle" 
                 title="Retrieve Password"
                 hoverable>
                    <a-steps :current="current" size="small">
                      <a-step v-for="item in steps" :key="item.title" :title="item.title" />
                    </a-steps>
                    <div class="registedStyle">
                        <a-form-model ref="ruleForm" :model="ruleForm" :rules="rules" v-bind="layout">
                            <a-form-model-item has-feedback label="ID " prop="ID" v-show="current==0?true:false">
                                <a-input v-model="ruleForm.ID" autocomplete="on" />
                            </a-form-model-item>
                            <a-form-model-item has-feedback label="Password" prop="pass" v-show="current!=0?true:false">
                              <a-input v-model="ruleForm.pass" type="password" autocomplete="off" />
                            </a-form-model-item>
                            <a-form-model-item has-feedback label="Confirm" prop="checkPass" v-show="current!=0?true:false">
                              <a-input v-model="ruleForm.checkPass" type="password" autocomplete="off" />
                            </a-form-model-item>
                            <a-form-model-item :wrapper-col="{ span: 16, offset: 1 }">
                                <a-button type="primary" @click="submitForm('ruleForm')">
                                    Submit
                                </a-button>
                                <a-button style="margin-left: 10px" @click="resetForm('ruleForm')">
                                    Reset
                                </a-button>
                                <a-button v-if="current > 0" style="margin-left: 8px" @click="prev('ruleForm')">
                                  Previous
                                </a-button>
                            </a-form-model-item>
                        </a-form-model>
                    </div>
                </a-card>
            </a-col>
        </a-row>
    </div>
</template>

<script>
export default {
  data() {
    let validateID = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the ID Number'));
      } else {
         var i = value
          var l=0;
          while(i >= 1){
            i=i/10;
            l++;
          }
          console.log(l)
          if (l !== 15) {
            callback(new Error("Please input correct ID number"));
          } else {
            callback();
          }
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please input the password again'));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("Two inputs don't match!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        ID:'',
        pass: '',
        checkPass: ''
      },
      idnumber: '',
      current: 0,
      rules: {
        ID: [{ validator: validateID, trigger: 'change' }],
        pass: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      },
      steps: [
        {
          title: 'Verification',
        },
        {
          title: 'Reset',
        },
        {
          title: 'Done',
        },
      ],
    };
  },
  methods: {
    submitForm(formName) {
      if(this.current == 0){
        this.idnumber = this.ruleForm.ID
        this.$refs[formName].validate(valid => {
            if(this.idnumber != ''){
              this.$refs[formName].validate(valid => {
                var params = new URLSearchParams()
                params.append('idnumber', this.idnumber)
                this.axios.post('http://localhost/api/user/userInfo/forgetPass_ID.php',params).then(res=>{
                      if(res.data[0] == 200){
                        this.$refs[formName].resetFields()
                        this.$message.success('Success! Using ' + res.data[1].toFixed(4) + 's')
                        this.current++
                      }
                      else if(res.data[0] == 404){
                        this.$notification['error']({
                              message: 'Error',
                              description: res.data[1],
                        })
                      }
                  }).catch(err=>{
                  this.$notification['error']({
                        message: 'Error',
                        description:
                        'Error Server!',
                    })
                })
              })
            } else {
              return false;
            }
          })
        } else {
          if (this.ruleForm.pass != '' && this.ruleForm.checkPass != '') {
            var params = new URLSearchParams()
            params.append('password', this.ruleForm.pass)
            params.append('idnumber', this.idnumber)
            this.axios.post('http://localhost/api/user/userInfo/forgetPass_Pass.php',params).then(res=>{
                if(res.data[0] == 200){
                  this.current++
                  this.$router.push({
                      name: 'Login',
                      }).catch(() =>{}).then(
                        this.$notification['success']({
                            message: 'Success',
                            description:
                            'Please re-log! Using ' + res.data[1].toFixed(4) + 's',
                        })
                      )
                } else {
                  this.$notification['error']({
                    message: 'Error',
                    description: res.data[1],
                  })
                }
            }).catch(err=>{
              this.$notification['error']({
                    message: 'Error',
                    description:
                    'Error Server!',
                })
              })
          } else {
            return false
          }
        }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    prev(formName){
      this.$refs[formName].resetFields()
      this.current--
    }
  },
};
</script>

<style scoped>
    .cardStyle{
        width: 70%;
        position: relative;
        top: 25%;
    }
    .registedStyle{
        position: relative;
        left: 40px;
        padding-top: 20px;
    }
    .imgStyle{
        position: absolute;
        left: 20%;
        width: 400px;
        bottom:10%;
    }
</style>