<template>
  <div>
    <a-card title="Income" hoverable>
      <a-row :gutter="16">
        <a-col :span="8">
          <a-statistic title="Active Users" :value="total_user" style="margin-right: 50px" />
        </a-col>
        <a-col :span="8">
          <a-statistic title="Income (CNY)" :precision="2" :value="income" />
        </a-col>
        <a-col :span="8">
          <a-statistic
              title="Year-on-year"
              :value="value"
              :precision="2"
              suffix="%"
              :value-style="valueStyle"
              style="margin-right: 50px"
            >
            <template #prefix>
              <a-icon :type="type" />
            </template>
          </a-statistic>
        </a-col>
      </a-row>
    </a-card>
  </div>
</template>

<script>
export default {
    data(){
        return{
            total_user: 0,
            income: 0,
            value: 0,
            type: 'arrow-up',
            valueStyle:{
              color: '#3f8600'
            }
        }
    },
    methods:{
      getIncome(){
        this.axios.post('http://localhost/api/manager/income.php').then(res=>{
          this.income = res.data[0]
          this.total_user = res.data[1]
          this.value = res.data[2] * 100
          this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Search income successfully',
            description:
            'Using ' + res.data[3].toFixed(4) + 's',
            duration: 2,
          });
        })
      }
    },
    mounted(){
      this.getIncome()
    }
}
</script>