<template>
  <div>
    <a-menu
      style="width: 110%;"
      :open-keys.sync="openKeys"
      mode="inline"
    >
      <a-sub-menu key="sub1">
        <span slot="title"><a-icon type="home" /><span>Hospital Information</span></span>
        <a-menu-item key="1">
          <router-link to="/Main">
            Introduction
          </router-link>
        </a-menu-item>
        <a-menu-item-group key="g">
          <template slot="title"><span>COVID-19</span> </template>
          <a-menu-item key="2" @click="handleTips1">
            Fever Clinic 
          </a-menu-item>
          <a-menu-item key="3">
            <router-link to="/VV">
            Vaccine Appointment
            </router-link>
          </a-menu-item>
          <a-sub-menu key="sub2" title="NAT">
            <a-menu-item key="4">
              <router-link to="/Appointment">
              Appointment
              </router-link>
            </a-menu-item>
            <a-menu-item key="5">
              <router-link to="/Result">
              Result
              </router-link>
            </a-menu-item>
          </a-sub-menu>
        </a-menu-item-group>
      </a-sub-menu>
      <a-sub-menu key="sub3">
        <span slot="title"><a-icon type="check-circle" /><span>Reservation Service</span></span>
        <a-menu-item key="6" @click="handleTips2">
          <router-link to="/AR">
            Appointment Register
          </router-link>
        </a-menu-item>
        <a-menu-item key="7">
          <router-link to="/RR">
            Reservation Record
          </router-link>
        </a-menu-item>
        <a-menu-item key="8">
          <router-link to="/OP">
            Outpatient Payment
          </router-link>
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub4">
        <span slot="title"><a-icon type="solution" /><span>Setting</span></span>
        <a-menu-item key="9">
          <router-link to="/PI">
            Personal Information
          </router-link>
        </a-menu-item>
      </a-sub-menu>
    </a-menu>
  </div>
</template>
<script>
export default {
  data() {
    return {
      openKeys: ['sub1'],
      modelShow: false
    };
  },
  methods: {
    handleTips1(){
      var this_ = this
      this.$confirm({
        title: 'Sure to get Fever Clinic appointment?',
        onOk() {
          var week = this_.getWeek(this_.$moment().day())
          var params = new URLSearchParams()
          params.append('idnumber', localStorage.getItem('Authorization'))
          params.append('date',this_.$moment().format("YYYYMMDD"))
          params.append('week', week)
          this_.axios.post('http://localhost/api/user/Covid/fever.php', params).then(res=>{
            this_ .$notification['success']({
            message: 'Successful Appointment',
            description:
              'The doctor has made an appointment for you. You can check the time of the appointment in the form.Please seek medical advice and report in time! Using ' + res.data[1] +'s.',
            });
            this_ .$router.push({
              name: 'RR'
            }).catch(() =>{})
          })
        },
        onCancel() {
          this_ .$router.push({
            name: 'Main'
          }).catch(() =>{})
        },
      });
    },
    handleTips2(){
      const h = this.$createElement;
      this.$info({
        title: 'Warm Tip!',
        content: h('div', {}, [
          h('p', 'Distinguished clients and their families:'),
          h('p', "How do you do!In order to ensure your safety and everyone's safety, and do your best to prevent COVID-19, please pay attention to the following matters when you visit our hospital:"),
          h('p', '1. In order to reduce the number of people gathered, family members are not allowed to accompany the doctor in principle, and an escort is allowed under special circumstances.'),      
          h('p', '2. When entering the hospital, the patient should take the temperature measurement, show the health code, and fill in the COVID-19 screening registration form. The epidemiological history and symptoms must be truthfully informed.'),
          h('p', '3. Patients and accompanying personnel should wear masks correctly when entering the hospital.'),
          h('p', "4. Our hospital implements the real name system to make an appointment, and the patient's ID card must be used. If the identity does not match, the appointment will be regarded as invalid.")
        ])
      })
    },
    getWeek (value) {
      switch (value) {
        case 1:
          return 'Mon'
        case 2:
          return 'Tue'
        case 3:
          return 'Wed'
        case 4:
          return 'Thur'
        case 5:
          return 'Fri'
        case 6:
          return 'Sat'
        case 0:
          return 'Sun'
      }
    }
  }
};
</script>