<template>
  <div>
    <a-descriptions class="userStyle" bordered :column="6" title="Doctor Info">
      <a-descriptions-item label="Name" :span="3">
        {{info.name}}
      </a-descriptions-item>
      <a-descriptions-item label="Section" :span="3">
        {{info.section}}
      </a-descriptions-item>
      <a-descriptions-item label="Professional Title" :span="6">
        {{info.title}}
      </a-descriptions-item>
      <a-descriptions-item label="Registration Fee" :span="2">
        {{info.fee}} ￥
      </a-descriptions-item>
      <a-descriptions-item label="Gender" :span="2">
        {{info.gender}}
      </a-descriptions-item>
      <a-descriptions-item label="Age" :span="2">
        {{info.age}}
      </a-descriptions-item>
      <a-descriptions-item label="Rates" :span="6">
          <a-rate
            :count = '4'
            :value="info.rating" 
            disabled/>
      </a-descriptions-item>
      <a-descriptions-item label="E-mail" :span="6">
        {{info.mail}}
      </a-descriptions-item>
      <a-descriptions-item label="Time" :span="6">
        <a v-for="(item, i) in info.workday" :key = "i"><a-tag color="cyan">{{item}}</a-tag></a>
      </a-descriptions-item>
      <a-descriptions-item label="Registrate" :span="6">
          <a-badge color="green" :status="info.status"/>
           <a-button @click="handleRegistrate">Registrate</a-button>
      </a-descriptions-item>
    </a-descriptions>
    <a-drawer
      title="Select the Date"
      placement="right"
      :closable="false"
      :visible="visible"
      @close="onClose"
    >
      <p v-for="(item, i) in info.workday" :key="i"><a-button @click="handleButton(item)">{{item}}</a-button></p>
    </a-drawer>
    <a-modal
      title="Update Information"
      :footer='null'
      :centered='true'
      :visible='isShown'
      :closable='false'
    >
      <info-writing></info-writing>
    </a-modal>
  </div>
</template>

<script>
import infoWriting from './infoWriting.vue';
export default {
  components: { infoWriting },
  data(){
    return{
      info:{
        name: '',
        age: '',
        gender: '',
        section:'',
        title: '',
        fee: '',
        mail:'',
        time:'',
        rating: 0,
        status: 'processing',
        workday:[]
      },
      visible: false,
      isShown: false
    }
  },
  methods:{
    getInfo(){
      if(this.$route.params.doctor_id){
        var params = new URLSearchParams()
        params.append('doctor_id', this.$route.params.doctor_id)
        this.axios.post('http://localhost/api/user/doctor/info.php', params).then(res=>{
          // console.log(res)
          var data = res.data[0][0]
          var workday = res.data[1][0]
          this.info.name = data.doctor_name
          this.info.age = data.age
          this.info.section = data.department.replace(data.department[0],data.department[0].toUpperCase());
          this.info.fee = data.appointment_cost
          this.info.mail = data.email
          if(workday.Mon == 1){
            this.info.workday.push('Mon')
          } 
          if(workday.Tue == 1){
            this.info.workday.push('Tue')
          }
          if(workday.Wed == 1){
            this.info.workday.push('Wed')
          }
          if(workday.Thur == 1){
            this.info.workday.push('Thur')
          }
          if(workday.Fri == 1){
            this.info.workday.push('Fri')
          }
          if(workday.Sat == 1){
            this.info.workday.push('Sat')
          }
          if(workday.Sun == 1){
            this.info.workday.push('Sun')
          }
          if (data.gender == 0){
            this.info.gender = 'Female'
          } else {
            this.info.gender = 'Male'
          }
          this.info.title = data.position
          this.info.rating = Math.round(parseFloat(data.rating))
          this.$message.success('Search Successfully! Using ' + res.data[2].toFixed(4) + 's');
        })
      } else {
        this.$router.push({
        name: 'PI',
      }).catch(() =>{})
      }
    },
    handleRegistrate(){
      this.visible = true
    },
    onClose() {
      this.visible = false
    },
    handleButton(day){
      var day = this.getWeek(day)
      var date
      if(day <= this.$moment().day()){
        date = (parseInt(this.$moment().weekday(day).format("YYYYMMDD"))+7).toString()
      } else {
        date = this.$moment().weekday(day).format("YYYYMMDD")
      }
      var params = new URLSearchParams()
      params.append('doctor_id', this.$route.params.doctor_id)
      params.append('idnumber', localStorage.getItem('Authorization'))
      params.append('date', date)
      this.axios.post('http://localhost/api/user/doctor/register.php', params).then(res=>{
        if(res.data[0] == 200){
          this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Registed successfully',
            description:
            'Using ' + res.data[2] + 's',
            duration: 2,
            placement: 'topLeft'
          });
          this.isShown =  true
        } else {
          this.$notification['error']({
            message: 'Error',
            description: res.data
          });
        }
      })
    },
    getWeek (day) {
      switch (day) {
        case 'Mon':
          return 1
        case 'Tue':
          return 2
        case 'Wed':
          return 3
        case 'Thur':
          return 4
        case 'Fri':
          return 5
        case 'Sat':
          return 6
        case 'Sun':
          return 7
      }
    }
  },
  mounted(){
    this.getInfo()
  }
}
</script>

<style scoped>
  .userStyle{
      position: absolute;
      left: 30%;
      top: 20%;
      width: 700px;
  }
</style>