<template>
  <ve-wordcloud :data="chartData" :chartExtend="chartExtend"></ve-wordcloud>
</template>

<script>
export default {
  data() {
    return {
      chartData: {
        columns: ['key', 'value'],
        rows: []
      },
      chartExtend:{
        series: {
          drawOutOfBound: true,
        }
      }
    };
  },
   methods:{
    getData(){
      this.axios.post('http://localhost/api/manager/medicine_using.php').then(res=>{
        var list  = res.data[0]
        var rows = []
        var data = {}
        data['key'] = list[0][0]
        data['value'] = list[0][1]
        rows.push(data);
        data = {}
        data['key'] = list[1][0]
        data['value'] = list[1][1]
        rows.push(data);
        data = {}
        data['key'] = list[2][0]
        data['value'] = list[2][1]
        rows.push(data);
        data = {}
        data['key'] = list[3][0]
        data['value'] = list[3][1]
        rows.push(data);
        data = {}
        data['key'] = list[4][0]
        data['value'] = list[4][1]
        rows.push(data);
        data = {}
        data['key'] = list[5][0]
        data['value'] = list[5][1]
        rows.push(data);
        data = {}
        data['key'] = list[6][0]
        data['value'] = list[6][1]
        rows.push(data);
        data = {}
        data['key'] = list[7][0]
        data['value'] = list[7][1]
        rows.push(data);
        data = {}
        data['key'] = list[8][0]
        data['value'] = list[8][1]
        rows.push(data);
        data = {}
        data['key'] = list[9][0]
        data['value'] = list[9][1]
        rows.push(data);
        this.chartData.rows = rows
         this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Get wordcloud chart data successfully',
            description:
            'Using ' + res.data[1].toFixed(4) + 's',
            duration: 2,
            placement: 'topLeft'
        });
      })
    }
  },
  mounted(){
    this.getData()
  }
}
</script>

