<template>
  <a-select
    show-search
    :value="value"
    placeholder="Input doctor name"
    :default-active-first-option="false"
    :show-arrow="false"
    :filter-option="false"
    :allowClear='true'
    :not-found-content="fetching ? undefined : null"
    @change="handleChange"
    @search="handleSearch"
  >
  <a-spin v-if="fetching" slot="notFoundContent" size="small" />
    <a-select-option v-for="d in data" :key="d.value">
      {{ d.text }}
    </a-select-option>
  </a-select>
</template>
<script>
export default {
  data() {
    return {
      data: [],
      value: undefined,
      fetching: false
    };
  },
  methods: {
    handleSearch(value) {
      this.fetch(value, data => (this.data = data));
    },
    handleChange(value) {
      this.value = value;
      this.fetch(value, data => (this.data = data));
      var params = {}
      params['doctor_id'] = value
      var s = window.location.href;
      var b = s.split("/")[4];
      if(b == 'Doctor'){
        this.$router.push({
            name: 'Main',
            params
        }).catch(() =>{})
      } else {
        this.$router.push({
            name: 'Doctor',
            params
        }).catch(() =>{})
      }
    },
    fetch(value, callback) {
        let currentValue;
        let timeout;
        if (timeout) {
            clearTimeout(timeout);
            timeout = null;
        }
        currentValue = value;
        timeout = setTimeout(this.getList(value, callback, currentValue), 300);
    },
    getList(value, callback, currentValue){
        var params = new URLSearchParams()
        params.append('name', value)
        this.axios.post('http://localhost/api/user/doctor/search.php', params).then(res => {
            if (currentValue === value) {
              const result = res.data[1]
              const status = res.data[0]
              if(status == 200){
                const data = []
                result.forEach(r => {
                    data.push({
                        value: r.doctor_id,
                        text: r.doctor_name,
                    });
                });
                callback(data);
                this.fetching = false;
              }
            }
        });
    }
  }
};
</script>
