<template>
  <div>
    <div>
      <a-descriptions class="userStyle" :column="6" bordered title="User Info">
        <a-descriptions-item label="Name" :span='3'>
          {{info.name}}
        </a-descriptions-item>
        <a-descriptions-item label="Age" :span='1'>
          {{info.age}}
        </a-descriptions-item>
        <a-descriptions-item label="Gender" :span='2'>
          {{info.gender}}
        </a-descriptions-item>
        <a-descriptions-item label="Marital Status" :span='2'>
          {{info.marry}}
        </a-descriptions-item>
        <a-descriptions-item label="Vaccine Appointments" :span='4'>
          {{info.vAppointments}}
        </a-descriptions-item>
        <a-descriptions-item label="ID Number" :span='6'>
          {{info.idnumber}}
        </a-descriptions-item>
        <a-descriptions-item label="Phone Number" :span='6'> 
          {{info.pNumber}}
        </a-descriptions-item>
        <a-descriptions-item label="Fever?" :span='3'>
          <a-badge v-show="badgeShow" status="processing" :color="color1"/>
            {{info.fever}}
        </a-descriptions-item>
         <a-descriptions-item label="Risk Area?" :span='4'>
          <a-badge v-show="badgeShow" status="processing" :color="color2"/>
            {{info.high_risk_area}}
        </a-descriptions-item>
      </a-descriptions>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      info:{
        name: '',
        idnumber: '',
        fever: '',
        age: '',
        gender: '',
        marry: '',
        pNumber: '',
        ABalance:'',
        high_risk_area:'',
        vAppointments:''
      },
      color1: '',
      color2: '',
      badgeShow: false
    }
  },
  beforeCreate(){
      var params = new URLSearchParams()
      params.append('idnumber', localStorage.getItem('Authorization'))
      this.axios.post('http://localhost/api/user/setting/userInfo.php',params).then(res=>{
        var data = res.data[0][0]
        this.info.name = data.name
        this.info.idnumber = data.idnumber
        this.info.age = data.age
        this.info.pNumber = data.phone_number
        this.info.vAppointments = data.vaccine_times
        this.badgeShow = true
        if(data.marital_status == 1){
          this.info.marry = "Yes"
        } else {
          this.info.marry = "No"
        }
        if (data.fever == 1){
          this.info.fever = "NO"
          this.color1 = "blue"
        } else {
          this.info.fever = "YES"
          this.color1 = "red"
        }
        if (data.high_risk_area == 1){
          this.info.high_risk_area = "NO"
          this.color2 = "blue"
        } else {
          this.info.high_risk_area = "YES"
          this.color2 = "red"
        }
        if (data.gender == 1){
          this.info.gender = "Male"
        } else {
          this.info.gender = "Female"
        }
        this.$message.success('Search Successfully! Using ' + res.data[1].toFixed(4) + 's');
      })
  }
}
</script>
<style scoped>
  .userStyle{
      position: absolute;
      left: 30%;
      top: 20%;
      width: 700px;
  }
</style>