<template>
  <ve-pie :data="chartData"></ve-pie>
</template>

<script>
export default {
    data() {
    return {
      chartData: {
        columns: ['key', 'value'],
        rows: []
      }
    };
  },
  methods:{
    getData(){
      this.axios.post('http://localhost/api/manager/age.php').then(res=>{
        var rows = []
        var data = {}
        data['key'] = '14--24'
        data['value'] = res.data[1][0].V
        rows.push(data);
        data = {}
        data['key'] = '25--34'
        data['value'] = res.data[2][0].V
        rows.push(data);
        data = {}
        data['key'] = '35--44'
        data['value'] = res.data[3][0].V
        rows.push(data);
        data = {}
        data['key'] = '45--54'
        data['value'] = res.data[4][0].V
        rows.push(data);
        data = {}
        data['key'] = '55--64'
        data['value'] = res.data[5][0].V
        rows.push(data);
        data = {}
        data['key'] = '65--74'
        data['value'] = res.data[6][0].V
        rows.push(data);
        data = {}
        data['key'] = '>=75'
        data['value'] = res.data[7][0].V
        rows.push(data);
        this.chartData.rows = rows
        this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Get pie chart data successfully',
            description:
            'Using ' + res.data[8].toFixed(4) + 's',
            duration: 2,
            placement: 'topLeft'
        });
      })
    }
  },
  mounted(){
    this.getData()
  }
}
</script>

