<template>
    <div>
        <a-descriptions class="userStyle" :column="6" bordered>
            <a-descriptions-item label="Name" :span='2'>
                {{this.info.name}}
            </a-descriptions-item>
            <a-descriptions-item label="ID Number" :span='3'>
                {{this.info.idnumber}}
            </a-descriptions-item>
            <a-descriptions-item label="Total Cost" :span='1'>
                <a-statistic :precision="2" :value="info.cost" suffix="￥"/>
            </a-descriptions-item>
            <a-descriptions-item label="Medicine" :span='6'>
                <a-list :data-source="drugname" size='small'>
                    <a-list-item slot="renderItem" slot-scope="item">
                        {{item}}
                    </a-list-item>
                </a-list>
            </a-descriptions-item>
            <a-descriptions-item label="Item" :span='6' >
                 <a-list :data-source="inspection_item" size='small'>
                    <a-list-item slot="renderItem" slot-scope="item">
                        {{item}}
                    </a-list-item>
                </a-list>
            </a-descriptions-item>
        </a-descriptions>
    </div>
</template>

<script>
export default {
    data(){
        return{
            drugname: [],
            inspection_item: [],
            info:{
                name: '',
                idnumber: '',
                cost: 0.0
            },
            pagination:{
                pageSize: 10,
                hideOnSinglePage: true
            },
            loading: false,
            busy: false
        }
    },
    methods:{
        getList(){
            var params = new URLSearchParams()
            params.append('idnumber', localStorage.getItem('Authorization'))
            this.axios.post('http://localhost/api/user/OP/prescription.php',params).then(res=>{
                var data = res.data[0]
                if(res.data[2] == 200){
                    this.info.name = data[0].user_name
                    this.info.idnumber = data[0].idnumber
                    this.info.cost = data[0].total_cost
                    var list = []
                    for(var i = 0; i < data.length; i++){
                        list.push(data[i].drugname)
                    }
                    this.drugname = list
                    list = []
                    for(var i = 0; i < data.length; i++){
                        list.push(data[i].inspection_item)
                    }
                    this.inspection_item = list
                    this.$message.success('Search Successfully! Using ' + res.data[1].toFixed(4) + 's');
                } else if(res.data[2] == 404){
                    this.info.name = data[0].name
                    this.info.idnumber = data[0].idnumber
                    this.$message.success('Search Successfully! Using ' + 0 + 's');
                }
            })
        }
    },
    mounted(){
        this.getList()
    }
}
</script>

<style scoped>
    .userStyle{
        position: relative;
        width: 80%;
        left:10%
    }
</style>