<template>
  <div>
    <a-modal
      title="Update Information"
      :footer='null'
      :centered='true'
      :visible='isShown'
      :closable='false'
    >
      <info-writing></info-writing>
    </a-modal>
    <div class="tableStyle">
        <a-table 
          :columns="columns" 
          :data-source="data" 
          :pagination="pagination"
          @change="handleTableChange"
          :loading="loading"
          style="width: 90%">
          <span slot="rate" slot-scope="rating">    
            <a-rate
             :count = '4'
             :key="rating"
             :value="rating" 
             disabled/>
          </span>
            <template slot="action" slot-scope="text, record">
              <a-popconfirm
                v-if="data.length"
                title="Sure to get?"
                @confirm="() => handleApp(record)"
              >
                <a href="javascript:;">Get It</a>
              </a-popconfirm>
            </template>
            <div
              slot="filterDropdown"
              slot-scope="{ column }"
              style="padding: 8px"
            >
            <a-input
              :placeholder="`Search ${column.dataIndex}`"
              style="width: 188px; margin-bottom: 8px; display: block;"
              v-model = "searchText"
              @pressEnter="handleSearch"
            />
            <a-button
              type="primary"
              icon="search"
              size="small"
              style="width: 90px; margin-right: 8px"
              @click="handleSearch"
            >
              Search
            </a-button>
            <a-button size="small" style="width: 90px" @click="handleReset">
              Reset
            </a-button>
          </div>
          <a-icon
            slot="filterIcon"
            type="search"
            :style="{ color: '#108ee9'}"
          />
        </a-table>
    </div>
  </div>
</template>
<script>
import infoWriting from './infoWriting.vue';

const columns = [
  { title: 'Doctor Name', dataIndex: 'doctor_name', key: 'name'},
  { 
    title: 'Department', 
    dataIndex: 'department', 
    key: 'department',
    scopedSlots: {
      filterDropdown: 'filterDropdown',
      filterIcon: 'filterIcon',
      customRender: 'customRender',
    }
  },
  { title: 'Position', dataIndex: 'position', key: 'position'},
  { title: 'Age', dataIndex: 'age', key: 'age'},
  { title: 'Fee', dataIndex: 'appointment_cost', key: 'fee'},
  { title: 'Gender', dataIndex: 'gender', key: 'gender'},
  { title: 'Email', dataIndex: 'email', key: 'email'},
  {
    title: 'Rates',
    dataIndex: 'rating',
    scopedSlots: { customRender: 'rate' },
    width: 170
  },
  {
    title: 'Appointment',
    key: 'operation',
    fixed: 'right',
    align: 'center',
    scopedSlots: { customRender: 'action' },
  },
];

const data = [];

export default {
  components: { infoWriting },
  data() {
    return {
      data,
      columns,
      searchText: '',
      isShown: false,
      pagination:{
        hideOnSinglePage: true,
        pageSize: 8,
        total: 1000,
        current: 1
      },
      date: '',
      week: '',
      loading: false,
    }
  },
  methods:{
    handleApp(key){
      var params = new URLSearchParams()
      params.append('idnumber', localStorage.getItem('Authorization'))
      params.append('doctor_id', key.doctor_id)
      params.append('date', this.date)
      this.axios.post('http://localhost/api/user/AR/registed2.php', params).then(res=>{
        if(res.data[0] == 200){
          this.isShown =  true
          this.$notification['success']({
            message: res.data[1] + 'Using ' + res.data[1]
          })
        } else if(res.data[0] == 202){
          this.$notification['error']({
            message: res.data[1]
          })
        }
      })
    },
    getList(){
      var params = new URLSearchParams()
      params.append('date', this.date)
      params.append('week', this.week)
      params.append('num', this.pagination.pageSize)
      params.append('page', this.pagination.current)
      params.append('department', this.searchText)
      this.loading = true;
      this.axios.post('http://localhost/api/user/AR/registed1.php', params).then(res=>{
        this.pagination.total = res.data[0]
        var data = res.data[1]
        if(data != 404){
          data = data.map(item=>({
            ...item,
            gender: (item.gender == 1) ? 'Male' : 'Female',
            rating: Math.round(parseFloat(item.rating)),
            department: item.department.replace(item.department[0],item.department[0].toUpperCase())
          }))
          this.data = data
          this.$message.success('Search Successfully! Using ' + res.data[2].toFixed(4) + 's');
        } else {
          this.$message.success('Search Successfully! Using ' + 0 + 's');
          // console.log(res.data[1])
        }
        this.loading = false;
      })
    },
    handleTableChange(pagination){
      this.pagination.current = pagination.current
      this.pagination.pageSize = pagination.pageSize
      this.getList()
    },
    handleReset() {
      this.searchText = '';
      this.getList()
    },
    handleSearch() {
      this.pagination.current = 1
      this.getList()
    },
  },
  mounted(){
    this.date = localStorage.getItem('date')
    this.week = localStorage.getItem('day')
    this.getList()
  }
  
};
</script>

<style scoped>
  .tableStyle{
      position: relative;
      left: 50px;
      width: 1300px;
  }
</style>
<style>
  .ant-layout-sider {
    background: #f1f2f3 !important; 
  }
</style>

