<template>
    <div class="tabStyle">
        <a-tabs default-active-key="1" size="large" @change="handleTabs">
            <a-tab-pane key="1">
            <span slot="tab">
                <a-icon type="check-square" />
                Gotten Appointments
            </span>
                <a-table 
                 :columns="columns1" 
                 :data-source="data1"
                 :pagination="pagination"
                 :loading="loading1"
                 @change="handleTableChange1"
                 :rowKey="record=>record.dataIndex"
                >
                    <a slot="name" slot-scope="text">{{ text }}</a>
                    <span slot="customTitle"><a-icon type="smile-o" /> Name</span>
                    <template slot="action" slot-scope="text, record">
                      <a-popconfirm
                        v-if="data1.length"
                        title="Sure to cancel?"
                        @confirm="() => handleCancel(record)"
                      >
                        <a href="javascript:;">Cancel</a>
                      </a-popconfirm>
                    </template>
                </a-table>
            </a-tab-pane>
            <a-tab-pane key="2">
            <span slot="tab">
                <a-icon type="close-square" />
                Canceled Appointments
            </span>
                <a-table 
                 :columns="columns2" 
                 :data-source="data2"
                 :pagination="pagination"
                 :loading="loading2"
                 @change="handleTableChange2"
                >
                  <a slot="name" slot-scope="text">{{ text }}</a>
                  <span slot="customTitle"><a-icon type="smile-o" /> Name</span>
                </a-table>
            </a-tab-pane>
        </a-tabs>
        <a-affix :style="{ position: 'absolute', right: '-300px', top:'100px', width:'200px'}">
          <a-card title="Related web sites">
            You can NOT see appointments of vaccine and NAT here!
          </a-card>
        </a-affix>
    </div>
</template>


<script>
const columns1 = [
  {
    dataIndex: 'user_name',
    key: 'user_name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: 'ID Number',
    dataIndex: 'idnumber',
    key: 'idnumber',
  },
  {
    title: 'Doctor Name',
    dataIndex: 'doctor_name',
    key: 'doctor_name',
  },
  {
    title: 'Department',
    dataIndex: 'department',
    key: 'department',
  },
  {
    title: 'Date',
    dataIndex: 'date',
    key: 'date'
  },
  {
    title: 'Cancel',
    key: 'cancel',
    align: 'center',
    scopedSlots: { customRender: 'action' },
  },
];
const data1 = [
];
const columns2 = [
  {
    dataIndex: 'user_name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: 'ID Number',
    dataIndex: 'idnumber',
    key: 'idnumber',
  },
  {
    title: 'Doctor Name',
    dataIndex: 'doctor_name',
    key: 'doctor_name',
  },
  {
    title: 'Department',
    dataIndex: 'department',
    key: 'department',
  },
  {
    title: 'Time',
    dataIndex: 'Appointment_date',
    key: 'Appointment_date',
  }
];
const data2 = [
];
export default {
  data() {
    return {
      data1,
      columns1,
      data2,
      columns2,
      pagination:{
        pageSize: 6,
        hideOnSinglePage: true,
        current: 1,
        total: 0
      },
      time: '',
      loading1: false,
      loading2: false
     }
  },
  methods:{
    handleCancel(key){
      var ndate = this.$moment().format('YYYYMMDD')
      var date = key.date.split('-')
      var list = date[0] + date[1] + date [2]
      if(ndate - list < 0){
        var id = key.appointment_id
        var params = new URLSearchParams()
        params.append('id', id)
        this.axios.post('http://localhost/api/user/RR/cancel.php',params).then(res=>{
          if(res.data[0] == 200){
            this.$message.success('Cancel Successfully! Using ' + res.data[1].toFixed(4) + 's');
            this.getGottenData()
            this.getCanceledData()
          }
        })
      } else {
        this.$message.warning('You can NOT cancel that!');
      }
    },
    handleTabs(key){
      this.pagination.current = 1
      if(key == 2){
        this.getCanceledData()
      } else {
        this.getGottenData()
      }
    },
    getGottenData(){
      var params = new URLSearchParams()
      params.append('idnumber', localStorage.getItem('Authorization'))
      params.append('page', this.pagination.current)
      params.append('num', this.pagination.pageSize)
      this.load1 == true
      this.axios.post('http://localhost/api/user/RR/get.php',params).then(res=>{
        if(res.data[0] == 200){
          var data = res.data[2]
          this.pagination.total = res.data[1]
          data = data.map(item=>({
            ...item,
            // date: item.date.substring(0,4) + '/' + item.date.substring(4,6) + '/' + item.date.substring(6,8),
            department: item.department.replace(item.department[0],item.department[0].toUpperCase())
          }))
          this.data1 = data
          this.$message.success('Search Successfully! Using ' + res.data[3].toFixed(4) + 's');
        } else {
          this.data1 = data
          this.$message.success('Search Successfully! Using ' + 0 + 's');
        }
        this.load1 == false
      })
    },
    getCanceledData(){
      var params = new URLSearchParams()
      params.append('idnumber', localStorage.getItem('Authorization'))
      params.append('page', this.pagination.current)
      params.append('num', this.pagination.pageSize)
      this.load2 == true
      this.axios.post('http://localhost/api/user/RR/canceled.php',params).then(res=>{
        if(res.data[0] == 200){
          var data = res.data[2]
          this.pagination.total = res.data[1]
          data = data.map(item=>({
            ...item,
            // Appointment_date: item.Appointment_date.substring(0,4) + '/' + item.Appointment_date.substring(4,6) + '/' + item.Appointment_date.substring(6,8),
            department: item.department.replace(item.department[0],item.department[0].toUpperCase())
          }))
          this.data2 = data
          this.$message.success('Search Successfully! Using ' + res.data[3].toFixed(4) + 's');
        } else {
          // this.data1 = data
          this.pagination.total = 0
          this.$message.success('No data!');
        }
        this.load2 == false
      })
    },
    handleTableChange1(pagination){
      this.pagination.current = pagination.current
      this.pagination.pageSize = pagination.pageSize
      this.getGottenData()
    },
    handleTableChange2(pagination){
      this.pagination.current = pagination.current
      this.pagination.pageSize = pagination.pageSize
      this.getCanceledData()
    },
  },
  mounted(){
    this.getGottenData()
  }
}
</script>

<style scoped>
  .tabStyle{
      height: 400px;
      position: absolute;
      left: 20%;
      width: 60%
  }
</style>