<template>
    <div>
      <a-back-top :visibilityHeight="10"/>
        <a-layout v-if="$route.meta.keepalive">
          <a-layout-header style="background: #f0f2f5">
            <div class="headerStyle">
              <div v-if="loginShow">
                <a-input-search 
                  size="large"
                  placeholder="Input doctor id" 
                  style="width: 300px; margin-right: 400px;"
                  @search="onSearch"
                  v-if="inputShow"/>
                <userInput v-if="!inputShow" style="width: 300px; margin-right: 400px;"></userInput>
                <a-avatar v-if="inputShow" src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" style='padding-right: 10px'/>
                <a-tag :color="color">
                  Logged
                </a-tag>
                <a-popconfirm placement="bottomRight" ok-text="Yes" cancel-text="No" @confirm="confirm">
                  <template slot="title">
                    <p>Do you want to log out?</p>
                  </template>
                  <a-button size="small">Log Out</a-button>
                </a-popconfirm>
              </div>
              <a-tag v-if="!loginShow" color="red">
                Not Logged 
              </a-tag>
            </div>
          </a-layout-header>
            <a-layout-content style="padding: 0 50px; minHeight: 650px">
              <a-layout style="padding: 24px 0;">
                <a-layout-sider v-if="$route.meta.skeepalive">
                    <sidebar></sidebar>
                </a-layout-sider>
                <a-layout-content>
                  <router-view></router-view>
                </a-layout-content>
              </a-layout>
            </a-layout-content>
          <a-layout-footer style="background-color: white">
            <vfooter></vfooter>
          </a-layout-footer>
        </a-layout>
        <a-drawer
          title="Doctor Information"
          placement="right"
          :closable="false"
          :visible="visible"
          @close="onClose"
          width='350px'
        >
          <p>ID: {{info.id}}</p>
          <p>Name: {{info.name}}</p>
          <p>Age: {{info.age}}</p>
          <p>Gender: {{info.gender}}</p>
          <p>Phone Number: {{info.phone_number}}</p>
          <p>Locaction: {{info.location}}</p>
          <p>Fee: {{info.fee}}￥</p>
          <p>Email: {{info.email}}</p>
          <p>Department: {{info.department}}</p>
          <p>Position: {{info.position}}</p>
          <p>Rates: <a-rate :value="info.rates" :count = '4' disabled /></p>
          <p>Workday: <a v-for="(item, i) in info.workday" :key = "i"><a-tag color="cyan">{{item}}</a-tag></a></p>
        </a-drawer>
    </div>
</template>

<script>
import PubSub from 'pubsub-js'
import sidebar from '@/layouts/SideBar'
import userInput from '@/components/userInput'
import vfooter from '@/components/footer'
export default {
  data () {
    return {
      loginShow: false,
      inputShow: false,
      color: 'blue',
      visible: false,
      info:{
        id: '',
        name: '',
        age: '',
        gender: '',
        phone_number: '',
        location: '',
        fee: '',
        email: '',
        department: '',
        position: '',
        rates: 0,
        workday: []
      },
      username: ''
    }
  },
    components:{
    sidebar, userInput, vfooter
  },
  created() {
      var s = window.location.href;
      var b = s.split("/")[4];
      if(b != "Registed" && b != "ForgetPassword" && b != "login"){
        this.loginShow = true
        if(b == "Controller"){
          this.color = 'green'
          this.inputShow = true
        }
      }
      PubSub.subscribe('userEvent',(name,data) => {
        let token = localStorage.getItem('Authorization');
        if(token != '0000' && token != null) {
          this.loginShow = true
          if(token == 1) {
            this.color = 'green'
            this.inputShow = true
          } else {
            this.color = 'blue'
            this.inputShow = false
          }
        } else {
          this.loginShow = false
        }
      }
    )
  },
  methods:{
    confirm() {
      this.$message.success('Logout Success!', 1)
      this.$router.push({
        name: 'Login',
      }).catch(() =>{})
    },
    getDoctorList(value){
      var params = new URLSearchParams()
      params.append('id', value)
      this.axios.post('http://localhost/api/manager/doctorInfo.php', params).then(res=>{
        var data = res.data[0][0]
        this.info.id = data.id
        this.info.name = data.name
        this.info.age = data.age
        this.info.phone_number = data.phone_number
        this.info.location = data.location
        this.info.fee = data.fee
        this.info.email = data.email
        this.info.position = data.position
        this.info.rates = Math.round(parseFloat(data.avg_rating))
        // console.log(this.info.rates)
        this.info.department = data.department
        if(data.Mon == 1){
          this.info.workday.push('Mon')
        } 
        if(data.Tue == 1){
          this.info.workday.push('Tue')
        }
        if(data.Wed == 1){
          this.info.workday.push('Wed')
        }
        if(data.Thur == 1){
          this.info.workday.push('Thur')
        }
        if(data.Fri == 1){
          this.info.workday.push('Fri')
        }
        if(data.Sat == 1){
          this.info.workday.push('Sat')
        }
        if(data.Sun == 1){
          this.info.workday.push('Sun')
        }
        if (data.gender == 1){
          this.info.gender = 'Male'
        } else {
          this.info.gender = 'Female'
        }
        this.$notification.open({
            icon: <a-icon type="smile" style="color: #108ee9" />,
            message: 'Search doctor information successfully',
            description:
            'Using ' + res.data[1] + 's',
            duration: 2,
            placement: 'topLeft'
        });
      })
    },
    onSearch(value) {
      if(/^\d+$/.test(value)){
        this.visible = true;
        this.getDoctorList(value)
      } else {
          this.$notification['warning']({
            message: 'Warning',
            description:
              'Please input doctor ID!',
          })
      }
    },
    onClose() {
      this.info.workday = []
      this.visible = false;
    }
  }
}
</script>

<style>
  .headerStyle{
    position: absolute;
    right: 20px;
  }
  .events {
    list-style: none;
    margin: 0;
    padding: 0;
  }
</style>