<template>
    <a-table 
      :columns="columns" 
      :data-source="data" 
      :pagination="pagination"
      @change="handleTableChange"
      :scroll="{ y: 220 }"
      :loading="loading"
      size="small">
      <template slot="title">
        Medicine
      </template>
        <div
          slot="filterDropdown"
          slot-scope="{ column }"
          style="padding: 8px"
        >
        <a-input
          :placeholder="`Search ${column.dataIndex}`"
          style="width: 188px; margin-bottom: 8px; display: block;"
          v-model = "searchText"
          @pressEnter="handleSearch"
        />
        <a-button
          type="primary"
          icon="search"
          size="small"
          style="width: 90px; margin-right: 8px"
          @click="handleSearch"
        >
          Search
        </a-button>
        <a-button size="small" style="width: 90px" @click="handleReset">
          Reset
        </a-button>
      </div>
      <a-icon
        slot="filterIcon"
        type="search"
        :style="{ color: '#108ee9'}"
      />
    </a-table>
</template>
<script>

const columns = [
  { 
    title: 'ID', 
    dataIndex: 'id', 
    key: 'department',
    scopedSlots: {
      filterDropdown: 'filterDropdown',
      filterIcon: 'filterIcon',
      customRender: 'customRender',
    }
  },
  { title: 'Name', dataIndex: 'drugname', key: 'drugname', ellipsis: true},
  { title: 'Form', dataIndex: 'form', key: 'form', ellipsis: true},
  { title: 'Price', dataIndex: 'price', key: 'price'},
  { title: 'Strength', dataIndex: 'strength', key: 'strength', ellipsis: true},
];

const data = [];

export default {
  data() {
    return {
      data,
      columns,
      searchText: '',
      pagination:{
        hideOnSinglePage: true,
        pageSize: 20,
        total: 10000,
        current: 1
      },
      loading: false,
    }
  },
  methods:{
    getList(){
      var params = new URLSearchParams()
      params.append('id', this.searchText)
      params.append('num', this.pagination.pageSize)
      params.append('page', this.pagination.current)
      this.loading = true;
      this.axios.post('http://localhost/api/manager/medicine.php', params).then(res=>{
        this.data = res.data[0]
        this.loading = false;
        this.$notification.open({
          icon: <a-icon type="smile" style="color: #108ee9" />,
          message: 'Search medicine successfully',
          description:
          'Using ' + res.data[1] + 's',
          duration: 2,
        });
      })
    },
    handleTableChange(pagination){
      this.pagination.current = pagination.current
      this.pagination.pageSize = pagination.pageSize
      this.getList()
    },
    handleReset() {
      this.searchText = '';
       this.pagination.total= 10000,
      this.getList()
    },
    handleSearch() {
      this.pagination.current = 1
      this.pagination.total= 1,
      this.getList()
    },
  },
  mounted(){
    this.getList()
  }
  
};
</script>

<style scoped>
  .highlight {
    background-color: rgb(255, 192, 105);
    padding: 0px;
  }
</style>

