<template>
  <div>
    <a-modal
      title="Update Information"
      :footer='null'
      :centered='true'
      :visible='isShown'
      :closable='false'
    >
      <info-writing></info-writing>
    </a-modal>
    <a-calendar class="cStyle" :fullscreen="false" @select="onSelect">
      <ul slot="dateCellRender" slot-scope="value" class="events">
        <li v-for="item in getListData(value)" :key="item.content">
          <a-badge :status="item.type" />
        </li>
      </ul>
    </a-calendar>
  </div>
</template>
<script>
import infoWriting from '../../../components/infoWriting.vue';
export default {
  components: { infoWriting },
  data(){
    return{
      month: '',
      date: '',
      isShown: false
    }
  },
  methods: {
    onSelect(value) {
      let diffnum = -this.$moment(this.$moment()).diff(this.$moment(value), 'days')
      if(diffnum <= 6 && diffnum >= 0 && this.$moment().date() != value.date()){
        var params = new URLSearchParams()
        params.append('idnumber', localStorage.getItem('Authorization'))
        params.append('date', value.format('YYYYMMDD'))
        this.axios.post('http://localhost/api/user/Covid/appointment.php', params).then(res=>{
          if(res.data[0] == 200){
            this.isShown =  true
            this.$notification['success']({
                message: 'Successful Appointment',
                description:
                'Has been successful for you! Please go to the designated location for a nucleic acid test! Use ' + res.data[1].toFixed(4) + 's',
            });
          } else if (res.data[0] == 202){
            this.$notification['warning']({
                message: 'Warning',
                description: res.data[1]
            });
          }
        })
      }
    },
    getListData(value) {
      let listData;
      let diffnum = -this.$moment(this.$moment()).diff(this.$moment(value), 'days')
      if(diffnum <= 6 && diffnum >= 0 && this.$moment().date() != value.date()){
        listData = [
            { type: 'success'}
          ];
      }
      return listData || [];
    }
  }
};
</script>