<template>
  <div>
    <div class="login">
      <h1 class="titleStyle">Log In</h1>
        <a-form-model 
        ref="formInline"
        layout="horizontal" 
        :model="formInline" 
        @submit="handleLogin" 
        @submit.native.prevent>
          <a-form-model-item prop="idnumber">
            <a-input v-model="formInline.idnumber" placeholder="ID Number" style="width:300px">
              <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
            </a-input>
          </a-form-model-item>
          <a-form-model-item prop="pass">
            <a-input-password 
            v-model="formInline.password" 
            type="password" 
            autocomplete="off" 
            placeholder="Password">
              <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
            </a-input-password>
          </a-form-model-item>
          <a-form-model-item>
            <a-button
              type="danger"
              html-type="submit"
              :disabled="formInline.user === '' || formInline.password === ''"
              class="loginStyle"
              ghost
            >
            <span style="color: white">Log in</span>
            </a-button>
          </a-form-model-item>
        </a-form-model>
        <a @click="handleRegisted">
          Register Now!
        </a>
        <span style="color: white">
          Or
        </span>
        <a @click="handleGuest">
          Forget Password!
        </a>
    </div>
    <background></background>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import background from '@/components/login_backgroud'
import PubSub from 'pubsub-js'

export default {
  name: 'Login',
  data () {
    return {
      formInline: {
        idnumber: '',
        password: '',
      }
    }
  },
  components:{
    background
  },
  beforeCreate () {
    localStorage.removeItem('Authorization');
    PubSub.publish('userEvent', false);
  },
  methods: {
    ...mapMutations(['storeLogin']),
    handleLogin () {
      let pass = this.formInline.password
      var params = new URLSearchParams()
      params.append('idnumber', this.formInline.idnumber)
      this.axios.post('http://localhost/api/user/userInfo/login.php',params).then(res=>{
        if (res.data == 404){
          this.$notification['error']({
              message: 'Error',
              description:
                'Your ID number and password are incorrect.Please re-enter!',
            });
        } else {
          var data = res.data[0][0]
          let password = data.password
          let idnumber = data.idnumber
          if (password == pass){
            this.userToken = idnumber; 
            this.storeLogin({ Authorization: this.userToken });
            PubSub.publish('userEvent', this.userToken);
            this.$message.success('Login Success! Using ' + res.data[1].toFixed(4) + 's', 1)
            if(this.userToken == 1){
              this.$router.push({
                name: 'Controller',
              }).catch(() =>{})
            } else {
              this.$router.push({
                name: 'Main',
              }).catch(() =>{})
            }
          } else {
              this.$notification['error']({
              message: 'Error',
              description:
                'Your ID number and password are incorrect.Please re-enter!',
            });
          }
        }
      }).catch(err=>{
        this.$notification['error']({
              message: 'Error',
              description:
              'Error Server!',
          })
      })
    },
    handleRegisted () {
      this.userToken = '0000'
      this.storeLogin({ Authorization: this.userToken })
      this.$router.push({
        name: 'Regist'
      }).catch(() =>{})
    },
    handleGuest () {
      this.userToken = '0000'
      this.storeLogin({ Authorization: this.userToken })
      this.$router.push({
        name: 'ForgetPassword'
      }).catch(() =>{})
    }
  }
}
</script>

<style scoped>
  .login{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 99999;
  }
  .loginStyle{
    width: 300px;
  }
  .rejistStyle{
    position: relative;
    left: 81px
  }
  .guestStyle{
    position: relative;
    left: 40px
  }
  .titleStyle{
    position: relative;
    text-align: center;
    color: azure;
  }
</style>
