<template>    
  <a-form-model
    ref="ruleForm"
    :model="form"
    :rules="rules"
    :keyboard='false'
  >
    <a-form-model-item label="Do you have a fever?" prop="resource1">
        <a-radio-group v-model="form.resource1">
            <a-radio value="0">
            Yes
            </a-radio>
            <a-radio value="1">
            No
            </a-radio>
        </a-radio-group>
    </a-form-model-item>
    <a-form-model-item label="Have you traveled to a high risk area within 14 days?" prop="resource2">
        <a-radio-group v-model="form.resource2">
            <a-radio value="0">
            Yes
            </a-radio>
            <a-radio value="1">
            No
            </a-radio>
        </a-radio-group>
    </a-form-model-item>
    <a-form-model-item>
        <a-button type="primary" @click="onSubmit">
            Submit
        </a-button>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
export default {
  data() {
    return {
      form: {
        resource1: '',
        resource2: '',
      },
      rules: {
        resource1: [
          { required: true, message: 'Please select it!', trigger: 'change' },
        ],
        resource2: [
          { required: true, message: 'Please select it!', trigger: 'change' },
        ],
      },
    };
  },
  methods: {
    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          var params = new URLSearchParams()
          params.append('idnumber', localStorage.getItem('Authorization'))
          params.append('fever', this.form.resource1)
          params.append('risk', this.form.resource2)
          this.axios.post('http://localhost/api/user/Covid/update.php', params).then(res=>{
            if(res.data[0] == 200){
              this.$router.push({
                name: 'RR',
              }).catch(() =>{})
            }
          })
        } else {
          // console.log('error submit!!');
          return false;
        }
      });
    }
  },
};
</script>