<template>
    <div>
        <a-row>
            <a-row type="flex" justify="space-around" align="middle" :gutter="[48,48]">
                <a-col :span="24">
                    <flip-clock/>  
                </a-col>
            </a-row>
            <a-row type="flex" justify="space-around" align="middle" :gutter="[48,48]">
                <a-col :span="12">
                    <Income></Income>
                </a-col>
                <a-col :span="12">
                    <averrates></averrates>
                </a-col>
            </a-row>
            <a-row type="flex" justify="space-around" align="middle" :gutter="[48,48]">
                <a-col :span="12">
                    <medicine></medicine>
                </a-col>
                <a-col :span="12">
                    <items></items>
                </a-col>
            </a-row>
            <a-row type="flex" justify="space-around" align="middle" :gutter="[48,48]">
                <a-col :span="24">
                    <a-card
                        style="width:100%"
                        :tab-list="tabListNoTitle"
                        :active-tab-key="noTitleKey"
                        @tabChange="key => onTabChange(key, 'noTitleKey')"
                        >
                        <p v-if="noTitleKey === 'Proportion of positive nucleic acid'">
                            <pie-chart></pie-chart>
                        </p>
                        <p v-else-if="noTitleKey === 'The reservation number'">
                            <histogram-chart></histogram-chart>
                        </p>
                        <p v-else-if="noTitleKey === 'The number of reservations in the past three years'">
                            <line-chart></line-chart>
                        </p>
                        <p v-else-if="noTitleKey === 'Frequency of medication'">
                            <word-cloud></word-cloud>
                        </p>
                    </a-card>
                </a-col>
            </a-row>
        </a-row>
        <a-back-top :visibilityHeight='100'/>
    </div>
</template>
<script>
import FlipClock from 'kuan-vue-flip-clock'
import Income from '@/components/Controller/income.vue'
import Items from '../components/Controller/items.vue'
import Medicine from '../components/Controller/medicine.vue'
import averrates from '../components/Controller/aver_rates.vue'
import PieChart from '../components/Controller/pieChart.vue'
import HistogramChart from '../components/Controller/histogramChart.vue'
import LineChart from '../components/Controller/lineChart.vue'
import WordCloud from '../components/Controller/wordCloud.vue'

export default {
    components:{ Medicine, Items, Income, averrates, FlipClock, PieChart, HistogramChart, LineChart, WordCloud },
    data(){
        return{
            tabListNoTitle: [
                {
                    key: 'Proportion of positive nucleic acid',
                    tab: 'Proportion of positive nucleic acid',
                },
                {
                    key: 'The reservation number',
                    tab: 'The reservation number',
                },
                {
                    key: 'The number of reservations in the past three years',
                    tab: 'The number of reservations in the past three years',
                },
                {
                    key: 'Frequency of medication',
                    tab: 'Frequency of medication',
                },
            ],
            key: 'tab1',
            noTitleKey: 'The reservation number',
        }
    },
    methods: {
    onTabChange(key, type) {
    //   console.log(key, type);
      this[type] = key;
    },
  },
}
</script>
