<template>
  <a-table 
   :data-source="data" 
   :columns="columns"
   :pagination="pagination"
   bordered
   :rowKey="record=>record.dataIndex"
   :scroll="{ y: 165 }">
   <template slot="title">
      Items
    </template>
  </a-table>
</template>

<script>
const data = [];
const columns= [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Inspection Item',
    dataIndex: 'inspection_item',
    key: 'inspection_item'
  },
  {
    title: 'Location',
    dataIndex: 'loc',
    key: 'loc',
  },
  {
    title: 'Fee',
    dataIndex: 'fee',
    key: 'fee',
    sorter: (a, b) => a.fee - b.fee,
  },
];
export default {
  data() {
    return {
      data,
      columns,
      pagination:{
          hideOnSinglePage: true,
          pageSize: 13
      }
    };
  },
  methods: {
    getItems(){
      this.axios.post('http://localhost/api/manager/item.php').then(res=>{
        this.data = res.data[0]
        this.$notification.open({
          icon: <a-icon type="smile" style="color: #108ee9" />,
          message: 'Search items successfully',
          description:
          'Using ' + res.data[1] + 's',
          duration: 2,
        });
      })
    }
  },
  mounted(){
    this.getItems()
  }
};
</script>
<style scoped>
.highlight {
  background-color: rgb(255, 192, 105);
  padding: 0px;
}
</style>
