<template>
  <ve-line :data="chartData"></ve-line>
</template>

<script>
export default {
    data() {
        return {
            chartData: {
                columns: ['key', 'value'],
                rows: []
            }
        }   
    },
    methods:{
        getData(){
            this.axios.post('http://localhost/api/manager/apoint.php').then(res=>{
                var rows = []
                var data = {}
                data['key'] = '2018'
                data['value'] = res.data[0]
                rows.push(data);
                data = {}
                data['key'] = '2019'
                data['value'] = res.data[1]
                rows.push(data);
                data = {}
                data['key'] = '2020'
                data['value'] = res.data[2]
                rows.push(data);
                this.chartData.rows = rows
                this.$notification.open({
                    icon: <a-icon type="smile" style="color: #108ee9" />,
                    message: 'Get line chart data successfully',
                    description:
                    'Using ' + res.data[3].toFixed(4) + 's',
                    duration: 2,
                    placement: 'topLeft'
                });
            })
        }
    },
    mounted(){
        this.getData()
    }
}
</script>

